import React from "react";
import "./App.css";
import * as Survey from "survey-react";
import "survey-react/survey.css";
import "survey-react/modern.css";
import Result from "./Result";
import data from "./data/data.json";

class SurveyComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = { isCompleted: false };
        this.result = "";
        this.onCompleteComponent = this.onCompleteComponent.bind(this);
    }
    onCompleteComponent(survey, options) {
        this.setState({ isCompleted: true });
        this.result = survey.data;
    }
    render() {
        let json = data[0];
        document.title = data[0].title;
        var surveyRender = !this.state.isCompleted ? (
            <Survey.Survey
                json={json}
                showCompletedPage={false}
                onComplete={this.onCompleteComponent}
                pageNextText="다음"
                pagePrevText="이전"
                completeText="완료"
            />
        ) : null;
        var onCompleteComponent = this.state.isCompleted ? <CompletePage result={this.result} /> : null;
        return (
            <div>
                {surveyRender}
                {onCompleteComponent}
            </div>
        );
    }
}
class CompletePage extends React.Component {
    render() {
        let quizResult = this.props.result;
        let quizSum = 0;
        let quizAverage = 0;

        for (var k in quizResult["quiz"]) {
            if (quizResult["quiz"].hasOwnProperty(k)) {
                quizSum += parseInt(quizResult["quiz"][k]);
            }
        }
        quizAverage = quizSum / Object.keys(quizResult["quiz"]).length;
        return (
            <div>
                <Result result={quizAverage} />
            </div>
        );
    }
}

export default function App() {
    return (
        <div className="App">
            <SurveyComponent />
        </div>
    );
}
